@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,300&display=swap');
*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}
/*font-family: 'Poppins', sans-serif;*/
/*font-family: 'Poppins', sans-serif;*/
/*Contact Us*/
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
/*pnf css*/
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/*//navbar css//*/
.navbar{
  font-family:'Poppins', sans-serif;
  font-size: 18px;
  line-height: 27px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow:0 8px 6px -6px gray;
  border-bottom: 0 solid rgb(132, 231, 132) !important;
}
.nav-link{
  font-weight: 300 !important;
}
.active{
  border-bottom: 2px solid black;
}
.navbar-brand{
  font-weight: 700;
  font-family: 'roboto' sans-serif;
  letter-spacing: 3px;
}
/*footer.css*/
.footer{
  color:white;
  padding:25px;
background:linear-gradient(to right,#434343,#000000);
}
.footer a{
  text-decoration: none;
  color:white;
  padding: 10px;
}
.footer a:hover{
  color:magenta;
  border-bottom: 1px solid magenta;
}
